.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient(ellipse at center, #050510 0%, #0a0a1a 30%, #000000 100%);
  padding: 20px;
  position: relative;
  overflow: hidden;
}

/* Galaxy and Orion Stars Background */
.galaxy-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
  background: radial-gradient(ellipse at center, #050510 0%, #0a0a1a 30%, #000000 100%);
}

/* Cosmic Dust Particles */
.cosmic-dust {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.dust-particle {
  position: absolute;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.8) 0%, rgba(135, 206, 235, 0.4) 50%, transparent 100%);
  border-radius: 50%;
  animation: dustFloat 8s ease-in-out infinite;
  filter: blur(0.5px);
}

.dust-1 {
  top: 10%;
  left: 20%;
  width: 1px;
  height: 1px;
  animation-delay: 0s;
  animation-duration: 12s;
}

.dust-2 {
  top: 30%;
  left: 70%;
  width: 0.5px;
  height: 0.5px;
  animation-delay: 2s;
  animation-duration: 15s;
}

.dust-3 {
  top: 60%;
  left: 15%;
  width: 1.5px;
  height: 1.5px;
  animation-delay: 4s;
  animation-duration: 10s;
}

.dust-4 {
  top: 80%;
  left: 80%;
  width: 0.8px;
  height: 0.8px;
  animation-delay: 1s;
  animation-duration: 18s;
}

.dust-5 {
  top: 40%;
  left: 50%;
  width: 1px;
  height: 1px;
  animation-delay: 3s;
  animation-duration: 14s;
}

.dust-6 {
  top: 70%;
  left: 30%;
  width: 0.6px;
  height: 0.6px;
  animation-delay: 5s;
  animation-duration: 16s;
}

.dust-7 {
  top: 20%;
  left: 90%;
  width: 1.2px;
  height: 1.2px;
  animation-delay: 1.5s;
  animation-duration: 11s;
}

.dust-8 {
  top: 50%;
  left: 5%;
  width: 0.7px;
  height: 0.7px;
  animation-delay: 2.5s;
  animation-duration: 13s;
}

.dust-9 {
  top: 90%;
  left: 60%;
  width: 1px;
  height: 1px;
  animation-delay: 3.5s;
  animation-duration: 17s;
}

.dust-10 {
  top: 15%;
  left: 40%;
  width: 0.9px;
  height: 0.9px;
  animation-delay: 4.5s;
  animation-duration: 9s;
}

/* Galaxy Spiral */
.galaxy-spiral {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.spiral-arm {
  position: absolute;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(135, 206, 235, 0.6), transparent);
  border-radius: 50%;
  animation: spiralRotate 20s linear infinite;
}

.spiral-1 {
  transform: rotate(0deg);
  animation-delay: 0s;
}

.spiral-2 {
  transform: rotate(120deg);
  animation-delay: -6.7s;
}

.spiral-3 {
  transform: rotate(240deg);
  animation-delay: -13.3s;
}

/* Constellation Lines */
.constellation-lines {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.constellation-line {
  position: absolute;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(135, 206, 235, 0.4), transparent);
  animation: lineGlow 4s ease-in-out infinite;
}

.line-1 {
  top: 25%;
  left: 30%;
  width: 100px;
  transform: rotate(45deg);
  animation-delay: 0s;
}

.line-2 {
  top: 30%;
  left: 35%;
  width: 80px;
  transform: rotate(-30deg);
  animation-delay: 1s;
}

.line-3 {
  top: 40%;
  left: 50%;
  width: 120px;
  transform: rotate(60deg);
  animation-delay: 2s;
}

.line-4 {
  top: 45%;
  left: 55%;
  width: 90px;
  transform: rotate(-45deg);
  animation-delay: 0.5s;
}

.line-5 {
  top: 50%;
  left: 60%;
  width: 70px;
  transform: rotate(15deg);
  animation-delay: 1.5s;
}

/* Cosmic Waves */
.cosmic-waves {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.cosmic-wave {
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at center, rgba(138, 43, 226, 0.1) 0%, transparent 70%);
  animation: cosmicWave 15s ease-in-out infinite;
}

.wave-1 {
  animation-delay: 0s;
  animation-duration: 20s;
}

.wave-2 {
  animation-delay: -5s;
  animation-duration: 25s;
}

.wave-3 {
  animation-delay: -10s;
  animation-duration: 18s;
}

/* Star Clusters */
.star-clusters {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 4;
}

.star-cluster {
  position: absolute;
  width: 60px;
  height: 60px;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  animation: clusterPulse 6s ease-in-out infinite;
}

.cluster-1 {
  top: 20%;
  left: 20%;
  animation-delay: 0s;
}

.cluster-2 {
  top: 60%;
  right: 20%;
  animation-delay: 2s;
}

.cluster-3 {
  bottom: 20%;
  left: 60%;
  animation-delay: 4s;
}

