@keyframes twinkle {
  0%, 100% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

@keyframes orionGlow {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.3);
  }
}

@keyframes nebulaFlow {
  0% {
    transform: translateX(0) translateY(0) scale(1) rotate(0deg);
    opacity: 0.3;
  }
  25% {
    transform: translateX(10px) translateY(-5px) scale(1.1) rotate(90deg);
    opacity: 0.6;
  }
  50% {
    transform: translateX(-5px) translateY(10px) scale(0.9) rotate(180deg);
    opacity: 0.8;
  }
  75% {
    transform: translateX(15px) translateY(-10px) scale(1.05) rotate(270deg);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0) translateY(0) scale(1) rotate(360deg);
    opacity: 0.3;
  }
}

@keyframes nebulaGlow {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.6;
  }
}

@keyframes shootingStar {
  0% {
    transform: translateX(0) translateY(0) rotate(45deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateX(120vw) translateY(20vh) rotate(45deg);
    opacity: 0;
  }
}

@keyframes dustFloat {
  0% {
    transform: translateX(0) translateY(0) scale(0.5);
    opacity: 0.3;
  }
  25% {
    transform: translateX(20px) translateY(-10px) scale(1);
    opacity: 0.8;
  }
  50% {
    transform: translateX(-15px) translateY(15px) scale(0.7);
    opacity: 0.6;
  }
  75% {
    transform: translateX(25px) translateY(-20px) scale(1.2);
    opacity: 0.9;
  }
  100% {
    transform: translateX(0) translateY(0) scale(0.5);
    opacity: 0.3;
  }
}

@keyframes spiralRotate {
  0% {
    transform: rotate(0deg) scale(1);
    opacity: 0.6;
  }
  50% {
    transform: rotate(180deg) scale(1.1);
    opacity: 1;
  }
  100% {
    transform: rotate(360deg) scale(1);
    opacity: 0.6;
  }
}

@keyframes lineGlow {
  0%, 100% {
    opacity: 0.2;
    transform: scaleX(1);
  }
  50% {
    opacity: 0.8;
    transform: scaleX(1.2);
  }
}

@keyframes cosmicWave {
  0% {
    transform: scale(1) rotate(0deg);
    opacity: 0.1;
  }
  25% {
    transform: scale(1.2) rotate(90deg);
    opacity: 0.3;
  }
  50% {
    transform: scale(0.8) rotate(180deg);
    opacity: 0.5;
  }
  75% {
    transform: scale(1.1) rotate(270deg);
    opacity: 0.2;
  }
  100% {
    transform: scale(1) rotate(360deg);
    opacity: 0.1;
  }
}

@keyframes clusterPulse {
  0%, 100% {
    transform: scale(1);
    opacity: 0.1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.3;
  }
}

.login-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(15px);
  border-radius: 20px;
  padding: 40px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
  z-index: 2;
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
}

.login-header h1 {
  color: #333;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 10px;
}

.login-header p {
  color: #666;
  font-size: 16px;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: #333;
  font-weight: 600;
  font-size: 14px;
}

.form-group input {
  padding: 12px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 10px;
  font-size: 16px;
  transition: all 0.3s ease;
  /* background: rgba(255, 255, 255, 0.8); */
}

.form-group input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.error-message {
  color: #e74c3c;
  font-size: 14px;
  text-align: center;
  padding: 10px;
  background: rgba(231, 76, 60, 0.1);
  border-radius: 8px;
  border: 1px solid rgba(231, 76, 60, 0.2);
}

.login-button {
  padding: 14px 24px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.login-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.3);
}

.login-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

/* Admin Only Notice */
.admin-only-notice {
  margin-top: 15px;
  padding: 12px 16px;
  background: linear-gradient(135deg, rgba(255, 193, 7, 0.1) 0%, rgba(255, 152, 0, 0.1) 100%);
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 8px;
  text-align: center;
}

.admin-badge {
  display: inline-block;
  background: linear-gradient(135deg, #ffc107 0%, #ff9800 100%);
  color: #000;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.admin-only-notice p {
  color: #666;
  font-size: 13px;
  margin: 0;
  line-height: 1.4;
}

@media (max-width: 480px) {
  .login-card {
    padding: 30px 20px;
    margin: 10px;
  }
  
  .login-header h1 {
    font-size: 24px;
  }
}
